*,
*::before,
*::after {
	box-sizing: border-box;
}

* {
	scrollbar-width: thin;
	scrollbar-color: $black;
}

*::-webkit-scrollbar {
	width: 10px;
}

*::-webkit-scrollbar-track {
	background: transparent;
}

*::-webkit-scrollbar-thumb {
	background-color: $white;
	border-radius: $border-radius-24;
	box-shadow: $box-shadow-6;
	transition: all $transition-1;
}

*::-webkit-scrollbar-thumb:hover {
	background-color: $black-2;
}

*::-webkit-scrollbar-thumb:active {
	background-color: $main-color;
}

html {
	scroll-behavior: smooth;
	text-size-adjust: 100%;
}

html,
body {
	position: relative;
	user-select: none;
	transition: $transition-1;
}

body {
	position: relative;
	font-family: 'Poppins', sans-serif;
	@include fz24;
	color: $black;
	background-color: $black-gray-3;
	text-decoration: none;
	overflow: hidden;
}

a {
	color: $black;
	cursor: default;
}

ul,
ol,
li,
p,
h1,
h2,
h3,
h4,
h5,
h6 {
	margin: 0;
}

ul,
li {
	list-style: none;
	margin: 0;
	padding: 0;
}

p {
	margin: 14px 0;
}

button {
	color: $white;
	@include no-btn;
	display: inline-block;
}

img {
	display: flex;
	pointer-events: none;
	background-repeat: no-repeat;
	background-position: center center;
}

svg {
	display: block;
}

form {
	display: block;
	margin-top: 0em;
}

::selection {
	background: $main-color;
	color: $white;
}

::-moz-selection {
	background: $main-color;
	color: $white;
}

::-webkit-selection {
	background: $main-color;
	color: $white;
}

[data-title] {
	position: relative
}

[data-title]:after {
	content: attr(data-title);
	opacity: 0;
	visibility: hidden;
	z-index: $z-index-9;
	position: absolute;
	top: -3em;
	left: 50%;
	transform: translateX(-50%);
	padding: 2px 8px;
	white-space: nowrap;
	color: $black-2;
	border-radius: $border-radius-12;
	box-shadow: $box-shadow-6;
	background-color: rgba($white, 0.9);
	@include bgBlur;
	@include fz12;
	font-weight: 500;
}

[data-title]:hover:after {
	opacity: 1;
	visibility: visible;
	transition: $transition-0;
}


.loading {
	color: $white;
	background-color: rgba($black, 1);
	@include bgBlur;
	pointer-events: none;
	position: fixed;
	top: 0;
	left: 0;
	right: 0;
	bottom: 0;
	z-index: $z-index-8;
	animation-delay: 5.5s;
	animation-duration: 0.8s;
	animation-name: loadingNone;
	animation-timing-function: $transition-1-fun;
	animation-fill-mode: forwards;
}

.loading__inner {
	display: flex;
	flex-direction: column;
	justify-content: center;
	align-items: center;
	height: 100%;
}

.loading__icon {
	margin-bottom: 12px;

	svg {
		width: 78px;
		height: 78px;
	}
}

.loading__title {
	@include fz48;
	font-weight: 700;
	margin-bottom: 14px;
}

.loading__progress {
	width: 230px;
	height: 5px;
	background-color: rgba($white, 0.2);
	border-radius: $border-radius-34;
	position: relative;
	overflow: hidden;
	z-index: $z-index-0;

	&::before {
		content: "";
		position: absolute;
		top: 0;
		left: 0;
		bottom: 0;
		height: 100%;
		border-radius: $border-radius-34;
		background-color: $main-color;
		z-index: $z-index-1;
		animation-duration: 5.5s;
		animation-name: loading;
		animation-timing-function: $transition-1-fun;
		animation-fill-mode: forwards;
	}
}

@keyframes loading {
	0% {
		width: 0;
	}

	77% {
		width: 77%;
	}

	100% {
		width: 100%;
	}
}

@keyframes loadingNone {
	0% {
		opacity: 1;
		background-color: rgba($black, 1);
	}

	100% {
		opacity: 0;
		background-color: rgba($black, 0);
	}
}

.backgrounds,
.backgrounds2 {
	width: 100%;
	height: 100%;
	position: fixed;
	top: 0;
	left: 0;
	right: 0;
	bottom: 0;
	z-index: $z-index-minus-1;
}

.backgrounds2 {
	z-index: $z-index-6;
}

.backgrounds__inner,
.backgrounds2__inner {
	width: 100%;
	height: 100%;
}

.background__item,
.background2__item {
	width: 100%;
	height: 100%;

	img {
		width: 100%;
		height: 100%;
		object-fit: cover;
		opacity: 0;
		animation-duration: 0.5s;
		animation-delay: 2.0s;
		animation-name: loadImg;
		animation-timing-function: $transition-1-fun;
		animation-fill-mode: forwards;
	}
}

@keyframes loadImg {
	0% {
		opacity: 0;
	}

	100% {
		opacity: 1;
	}
}

.lockscreen {
	width: 100%;
	height: 100%;
	position: fixed;
	top: 0;
	left: 0;
	right: 0;
	bottom: 0;
	background-color: rgba($black, 0.2);
	@include bgBlur3;
	z-index: $z-index-7;
	pointer-events: none;
}

.lockscreen__inner {
	display: flex;
	flex-direction: column;
	justify-content: center;
	align-items: center;
	width: 100%;
	height: 100%;
}

.lockscreen__item {
	display: flex;
	flex-direction: column;
	justify-content: center;
	align-items: center;
	margin-bottom: 45px;
}

.lockscreen__avatar {
	width: 98px;
	height: 98px;
	border: 3px solid $gray;
	border-radius: $border-radius-16;
	overflow: hidden;
	background-color: rgba($white, 0.5);
	margin-bottom: 6px;
	pointer-events: all;
	transition: all $transition-1;

	img {
		height: 100%;
		width: 100%;
		object-fit: cover;
	}

	&:hover {
		box-shadow: $box-shadow-6;
	}
}

.lockscreen__name {
	font-weight: 600;
	color: $white;
	pointer-events: none;
	text-shadow: $box-shadow-6;
	margin-bottom: 10px;
}

.lockscreen__password-form {
	display: flex;
	flex-direction: column;
	justify-content: center;
	align-items: center;
	max-width: 280px;
	width: 100%;
	margin-bottom: 12px;
	pointer-events: all;
}

.lockscreen__password {
	color: $black;
	@include fz36;
	font-weight: 700;
	letter-spacing: 0.4px;
	width: 100%;
	height: 50px;
	border-top-left-radius: $border-radius-16;
	border-top-right-radius: $border-radius-16;
	border-left-width: 3px;
	border-right-width: 3px;
	border-top-width: 3px;
	border-bottom-width: 0;
	border-style: solid;
	border-color: $gray;
	background-color: rgba($white, 0.5);
	@include bgBlur;
	padding: 0 16px 0 16px;
	outline: none;
	transition: all $transition-1;

	&:hover,
	&:focus,
	&:valid {
		color: $white;
		border-color: $main-color;
		background-color: rgba($main-color, 0.7);
		box-shadow: $box-shadow-6;
	}
}

.login-form__btn {
	@include fz18;
	font-weight: 600;
	text-transform: uppercase;
	color: $black;
	width: 100%;
	height: 40px;
	border-bottom-left-radius: $border-radius-16;
	border-bottom-right-radius: $border-radius-16;
	border: none;
	background-color: rgba($gray, 0.9);
	@include bgBlur;
	pointer-events: fill;
	transition: all $transition-1;

	&:hover,
	&:focus {
		color: $white;
		background-color: rgba($main-color, 0.9);
		box-shadow: $box-shadow-6;
	}

	&:active {
		box-shadow: none;
	}
}

.lockscreen__hint {
	color: $white;
	@include fz18;
	user-select: text;
	pointer-events: all;

	span {
		pointer-events: none;
	}
}

.lockscreen__users {
	@include dFlex;
}

.lockscreen__user {
	position: relative;
}

.lockscreen__user-img {
	width: 56px;
	height: 56px;
	border: 3px solid $gray;
	border-radius: $border-radius-16;
	background-color: rgba($white, 0.5);
	overflow: hidden;
	pointer-events: all;
	cursor: pointer;
	margin: 0 16px 8px 16px;
	transition: all $transition-1;

	img {
		height: 100%;
		width: 100%;
		object-fit: cover;
	}
}

.lockscreen__user-title {
	text-align: center;
	white-space: nowrap;
	@include fz18;
	font-weight: 500;
	color: $white;
	padding: 2px 12px;
	border: 3px solid $main-color;
	pointer-events: none;
	border-radius: $border-radius-16;
	opacity: 0;
	background-color: rgba($main-color, 0.5);
	@include bgBlur;
	position: absolute;
	left: 50%;
	bottom: -40px;
	transform: translateX(-50%);
	transition: all $transition-1;
}

.lockscreen__user:hover {
	.lockscreen__user-img {
		color: $white;
		border: 3px solid $main-color;
		background-color: rgba($main-color, 0.7);
	}

	.lockscreen__user-title {
		opacity: 1;
	}
}

.lockscreen-widgets {
	position: fixed;
	top: 0;
	left: 0;
	right: 0;
	bottom: 0;
	width: 100%;
	height: 100%;
	pointer-events: none;
	z-index: $z-index-1;
}

.lockscreen-widgets__wrapper {
	width: 100%;
	height: 100%;
	display: flex;
	flex-direction: column;
	justify-content: space-between;
}

.lockscreen-widgets__inner {
	display: flex;
	justify-content: space-between;
	padding: 35px 48px;
}

.widget-time {
	@include fz76;
	font-weight: 700;
	color: $white;
	text-shadow: $box-shadow-6;
}

.lockscreen-widgets__item {
	width: calc(12%);

	.lockscreen-widgets__item-date {
		@include fz34;
		margin-top: -15px;
	}

	&:first-child {
		display: flex;
		flex-direction: column;
		align-items: center;
		margin-top: -12px;
		margin-left: -28px;
	}

	&:nth-child(2) {
		display: flex;
		justify-content: center;
	}

	&:last-child {
		display: flex;
		justify-content: flex-end;
	}

}

.widget-battery {
	width: 60px;
	height: 20px;
	position: relative;
}

.widget-battery-charge {
	width: 100%;
	height: 100%;
	border: 1px solid $white;
	border-radius: $border-radius-4;
	background-color: rgba($white, 0.2);
	overflow: hidden;
	@include bgBlur;
	position: relative;
	transition: all $transition-1;

	&::before,
	&::after {
		content: "";
		position: absolute;
		transition: all $transition-1;
	}

	&::before {
		width: 60%;
		height: 100%;
		background-color: rgba($white, 0.5);
		@include bgBlur;
	}

	&::after {
		content: "60%";
		@include fz12;
		color: $white;
		text-shadow: $box-shadow-6;
		top: -3.7px;
		left: 50%;
		transform: translateX(-50%);
	}
}

.widget-battery-charge-positive {
	position: absolute;
	width: 2px;
	height: 10px;
	border-top-right-radius: $border-radius-6;
	border-bottom-right-radius: $border-radius-6;
	background-color: $white;
	top: 50%;
	right: -2px;
	transform: translateY(-50%);
	z-index: $z-index-4;
	transition: all $transition-1;
}

.widget-weather-sun {
	width: 68px;
	height: 68px;
	background: linear-gradient(124deg, #FBBC37, #E4591B);
	border-radius: $border-radius-full;
	box-shadow: $box-shadow-6;
	position: relative;
	transition: all $transition-1;

	&::after,
	&::before {
		content: "";
		position: absolute;
		background: linear-gradient(136deg, $white, $black-gray-3);
		box-shadow: $box-shadow-6;
		border-radius: $border-radius-34;
		z-index: $z-index-1;
		transition: all $transition-1;
	}

	&::before {
		width: 16px;
		height: 16px;
		bottom: -3px;
		right: 5px;
		background: linear-gradient(136deg, #bdecfb, #00a2ff);
	}

	&::after {
		width: 50px;
		height: 20px;
		bottom: 16px;
		right: -10px;
	}
}

.widget-weather-title {
	@include fz34;
	font-weight: 700;
	color: $white;
	text-shadow: $box-shadow-6;
	margin-top: 4px;
}

.lockscreen-widgets__nav {
	padding: 35px 48px;
	align-self: flex-end;

	&-inner {
		width: 100%;
		height: auto;
		display: flex;
		align-items: center;
		justify-content: space-between;
		border-radius: $border-radius-16;
		transition: all $transition-1;
	}

	&-btn {
		color: $white;
		padding: 12px;
		border-radius: $border-radius-16;
		transition: all $transition-1;
		pointer-events: all;
		cursor: pointer;

		svg {
			width: 22px;
			height: 18px;
		}

		&:hover,
		&:focus {
			background-color: rgba($main-color, 0.7);
			box-shadow: $box-shadow-6;
		}

		&:active {
			box-shadow: none;
		}

	}
}

.noactive-lockscreen {
	opacity: 0;
	transition: all $transition-1;

	input,
	button,
	div,
	a {
		pointer-events: none;
	}
}

.wrong-password {
	border-color: $red;
	background-color: rgba($red, 0.4);
	color: $red;
	animation-name: animWrongPassword;
	animation-timing-function: $transition-1-fun;
	animation-duration: 0.5s;
	position: relative;

	&:valid {
		border-color: $red;
		background-color: rgba($red, 0.4);
		color: $red;
	}
}

@keyframes animWrongPassword {
	0% {
		left: 0;
	}

	35% {
		left: -5px;
	}

	70% {
		left: 5px;
	}

	100% {
		left: 0;
	}
}

.off {
	background-color: $black;
}

.off__inner {
	width: 100%;
	min-height: 100%;
	height: 100vh;
	display: flex;
	align-items: center;
	justify-content: center;
}

.off__btn {
	color: $white;
	padding: 22px;
	border-radius: $border-radius-full;
	transition: all $transition-1;

	svg {
		width: 56px;
		height: 56px;
	}

	&:hover {
		color: $black;
		background-color: $main-color;
	}
}

.modal {
	width: 220px;
	height: 300px;
	position: absolute;
	border-radius: $border-radius-16;
	background-color: rgba($white, 0.2);
	@include bgBlur4;
	box-shadow: $box-shadow-5;
	pointer-events: all;
	opacity: 0;
	overflow: hidden;
	z-index: $z-index-8;
	pointer-events: none;
	transition: opacity $transition-1;
}

.modal-top-info {
	display: flex;
	justify-content: space-between;
	align-items: center;
	padding: 5px 8px 8px 12px;
}

.modal-nav-wifi {
	right: 50px;
	bottom: 90px;
}

.modal-nav-wifi__title {
	@include fz18;
	color: $white;
	pointer-events: none;
}

.modal-nav-wifi__inner {
	overflow-y: scroll;
	display: flex;
	flex-direction: column;
	min-height: 100%;
	height: 100%;
	padding-bottom: 45px;
	transition: all $transition-1;
}

.modal-nav-wifi__item {
	display: flex;
	align-items: center;
	padding: 10px 8px 10px 12px;
	background-color: rgba($white, 0.9);
	word-break: break-all;
	cursor: pointer;
	transition: all $transition-1;

	&:hover {
		background-color: rgba($main-color, 0.9);
	}

	&:active {
		color: $white;
	}
}

.modal-nav-wifi__item-active {
	color: $white;
	background-color: rgba($main-color, 0.9);
}

.modal-nav-wifi__icon {
	svg {
		width: 22px;
		height: 22px;
	}
}

.modal-nav-wifi__wrapper {
	margin-left: 12px;
}

.modal-nav-wifi__wrapper-title {
	@include fz16;
	pointer-events: none;
}

.active-wifi-items {
	opacity: 0;
	pointer-events: none;
}

.checkbox {
	display: none;
}

.checkbox-style {
	display: block;
	width: 44px;
	height: 24px;
	border-radius: 17px;
	background-color: $black-gray-2;
	position: relative;
	transition: all $transition-1;
	cursor: pointer;

	&::before {
		content: "";
		position: absolute;
		top: 50%;
		left: 2px;
		transform: translateY(-50%);
		width: 20px;
		height: 20px;
		border-radius: 50px;
		background-color: $white;
		transition: all $transition-1;
	}
}

.checkbox:checked+.checkbox-style {
	background-color: $main-color;
}

.checkbox:checked+.checkbox-style::before {
	left: 22px;
}

.checkbox-active {
	display: none;
}

.checkbox-active-style {
	display: block;
	width: 44px;
	height: 24px;
	border-radius: 17px;
	background-color: $main-color;
	position: relative;
	transition: all $transition-1;
	cursor: pointer;

	&::before {
		content: "";
		position: absolute;
		top: 50%;
		right: 2px;
		transform: translateY(-50%);
		width: 20px;
		height: 20px;
		border-radius: 50px;
		background-color: $white;
		box-shadow: $box-shadow-6;
		transition: all $transition-1;
	}

}

.checkbox-active:checked+.checkbox-active-style {
	background-color: $black-gray-2;
}

.checkbox-active:checked+.checkbox-active-style::before {
	right: 22px;
}

.modal-active {
	pointer-events: all;
	opacity: 1;
}

.modal-modifi-pos {
	right: 50px;
	bottom: 90px;
}

.modal-modifi-pos2 {
	right: 20px;
	bottom: 70px;
}

.navigation {
	width: 100%;
	position: fixed;
	left: 50%;
	bottom: 0;
	transform: translateX(-50%);
	z-index: $z-index-6;
}

.navigation__inner {
	position: relative;
}

.navigation__items {
	display: flex;
	align-items: center;
	justify-content: center;
	width: 100%;
	height: auto;
	background-color: rgba($white, 0.3);
	@include bgBlur4;
	padding: 3px 0 4px 0;
}

.navigation__item {
	padding: 7px 10px;
	border-radius: $border-radius-12;
	transition-delay: 5.2s;
	transition: all $transition-1;

	&:hover,
	&:focus {
		background-color: rgba($white, 0.9);
		box-shadow: $box-shadow-6;
	}

	&:active {
		box-shadow: none;

		.navigation__item-icon {
			scale: 0.93;
		}
	}
}

.navigation__item-icon {
	display: block;
	width: 34px;
	height: 34px;
	color: $white;

	img,
	svg {
		width: 100%;
		height: 100%;
		object-fit: contain;
	}
}

.navigation-preview-item {
	opacity: 0;
	pointer-events: none;
	position: absolute;
	top: -148px;
	left: 50%;
	transform: translateX(-50%);
	z-index: $z-index-10;
	transition: all $transition-1;
	transition-delay: 0.4s;

	&__inner {
		border-radius: $border-radius-12;
		background-color: rgba($white, 0.6);
		overflow: hidden;
		box-shadow: $box-shadow-7;
		transition: all $transition-1;

		&:hover {
			background-color: rgba($white, 0.9);
		}
	}
}

.navigation-preview-item__top {
	display: flex;
	align-items: center;
	justify-content: space-between;
	padding: 3px 3px 3px 8px;
}

.navigation-preview-item__top-title {
	width: 133px;
	font-weight: 500;
	@include fz12;
	white-space: nowrap;
	overflow: hidden;
	text-overflow: ellipsis;
}

.navigation-preview-item__top-close {
	padding: 7px;
	border-radius: $border-radius-12;
	transition: $transition-1;

	svg {
		width: 16px;
		height: 16px;
	}

	&:hover {
		background-color: rgba($white, 1);
		box-shadow: $box-shadow-6;
	}

	&:active {
		color: $main-color;
	}
}

.navigation-preview-item__wrapper {
	img {
		width: 180px;
		height: 114px;
		object-fit: cover;
	}
}

.navigation-options {
	position: absolute;
	right: 0;
	bottom: 0;
	z-index: $z-index-0;
	height: 100%;
}

.navigation-options__inner {
	display: flex;
	justify-content: flex-end;
	width: auto;
	height: 100%;
}

.navigation-options__fold {
	height: 100%;
	width: 10px;
	border-top-left-radius: $border-radius-6;
	border-bottom-left-radius: $border-radius-6;
	background-color: rgba($white, 0.6);
	@include bgBlur4;
	transition: all $transition-1;

	&[data-title]:after {
		left: initial;
		transform: initial;
		right: 4px;
		top: -2.77em;
	}

	&:hover {
		background-color: rgba($white, 0.9);
	}

	&:active {
		scale: 0.93;
	}
}

.navigation-options__add-item {
	margin-top: 7px;
	margin-right: 6px;
	width: 14px;
	height: 40px;
	border-radius: $border-radius-6;
	background-color: rgba($white, 0.2);
	@include bgBlur4;
	transition: all $transition-1;

	&[data-title]:after {
		top: -3.3em;
	}

	&:hover {
