		height: 25px;
		background-color: $main-color;
		z-index: $z-index-1;
	}

	.noUi-target {
		width: 100%;
		height: 25px;
		border-radius: 0;
		background: initial;
		background-color: rgba($black-2, 0.3);
		border: none;
		outline: none;
		box-shadow: none;
		padding-right: 12px;
		padding-left: 11.5px;
	}

	.noUi-connect {
		background-color: $main-color;
	}

	.noUi-handle {
		width: 25px;
		height: 25px;
		right: -12px;
		top: 0;
		outline: none;
		border: none;
		border-radius: $border-radius-full;
		background-color: $white;
		box-shadow: $box-shadow-6;

		&::after,
		&::before {
			content: "";
			display: none;
			position: initial;
			height: initial;
			width: initial;
			background: initial;
			left: initial;
			top: initial;
		}

		&:hover {
			color: $black-2;
		}
	}
